import { useEffect, useState } from 'react';
import { getSessionMetaApiData } from '../../../../app/storage/session/metaApiData';
import { AvailableBanner } from '../../../../app/storage/session/shared';
import { versionStringToNumber } from '../../../../popup/shared/helpers/versionStringToNumber';

export function useAvailableBanner() {
  const [banners, setBanners] = useState<AvailableBanner[]>([]);

  useEffect(() => {
    async function getIsShown(): Promise<void> {
      const { availableBannersData, currentVersion, advMinVersion } =
        await getSessionMetaApiData();

      if (!availableBannersData) return;

      if (
        advMinVersion &&
        versionStringToNumber(currentVersion) >
          versionStringToNumber(advMinVersion)
      ) {
        return;
      }

      const availableBannerValue = Object.values(AvailableBanner);

      const availableBanners: AvailableBanner[] = availableBannersData
        .split(',')
        .filter((banner) =>
          availableBannerValue.includes(banner as AvailableBanner),
        ) as AvailableBanner[];

      setBanners(availableBanners);
    }
    void getIsShown();
  }, []);

  return banners;
}
