import { useEffect, useState } from 'react';
import { getSessionMetaApiData } from '../../../../../../../../app/storage/session/metaApiData';

export function useAndroidAppUrl() {
  const [link, setLink] = useState<string>('#');

  useEffect(() => {
    async function getIsShown(): Promise<void> {
      const { androidAppUrl } = await getSessionMetaApiData();

      setLink(androidAppUrl || '#');
    }
    void getIsShown();
  }, []);

  return link;
}
