import { NaruzhuBanner } from './NaruzhuBanner';
import { TelegramBanner } from './TelegramBanner';
import { UltaBanner } from './UltaBanner';
import { YouboostPremiumBanner } from './YouboostPremiumBanner';
import { useAvailableBanner } from '../hooks/useAvaliableBanner';
import { AvailableBanner } from '../../../../app/storage/session/shared';
import { YouboostAndroidBanner } from './YouboostPremiumBanner/ui/YouboostAndroidBanner';
import { YouboostNeoBanner } from './YouboostNeoBanner';

const bannersMap: Record<AvailableBanner, () => JSX.Element | null> = {
  [AvailableBanner.Naruzhu]: NaruzhuBanner,
  [AvailableBanner.Telegram]: TelegramBanner,
  [AvailableBanner.Ulta]: UltaBanner,
  [AvailableBanner.YouboostPremium]: YouboostNeoBanner,
  [AvailableBanner.YouboostAndroid]: YouboostAndroidBanner,
};

export function YouboostAds(): JSX.Element | null {
  const availableBanners = useAvailableBanner();

  if (!availableBanners.length) {
    return null;
  }

  const randomIndex = Math.floor(Math.random() * availableBanners.length);

  const SelectedBanner = bannersMap[availableBanners[randomIndex]];

  return SelectedBanner ? <SelectedBanner /> : null;
}
